import React, { Component } from 'react'
import Tab from './Tab'

export default class Tabs extends Component {
	static Tab = Tab;

	state = {
		tabs: [],
		prevActiveTab: {},
		activeTab: this.props.activeTab,
		tabsElements: [],
	};

	addTab = newTab => {
		let isNewTabFound

		for (const i in this.state.tabs) {
			const tab = this.state.tabs[i]

			if (tab.id === newTab.id) {
				isNewTabFound = true
				break
			}
		}

		if (!isNewTabFound) {
			this.setState((prevState, props) => {
				return {
					tabs: prevState.tabs.concat(newTab),
				}
			})
		}
	};

	removeTab = tabId => {
		this.setState((prevState, props) => {
			return {
				tabs: prevState.tabs.filter(tab => tab.id !== tabId),
			}
		})
	};

	onClick = tab => event => {
		this.setState((prevState, props) => {
			return {
				prevActiveTab: prevState.activeTab,
				activeTab: tab,
			}
		})
	};

	render () {
		return (
			<div>
				{/* tabs container */}
				<nav className={this.props.className}>
					<div>
						<ul>
							{this.state.tabs.map((tab, index) => (
								<li
									key={index}
									className={(tab.id === this.state.activeTab.id ? `${this.props.activeTabClass} ` : `${this.props.inactiveTabClass} `) + this.props.baseTabClass}
									onClick={this.onClick(tab)}
									id={tab.id}
									style={{
										display: 'inline-block',
										transition: 'all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
										cursor: 'pointer',
									}}>
									{tab.shortTitle
										? <a>
											<span className='hidden sm_block'>{tab.title}</span>
											<span className='sm_hidden'>{tab.shortTitle}</span>
										</a>
										: <a>
											{tab.title}
										</a>}
								</li>
							))}
						</ul>
					</div>
				</nav>

				{React.Children.map(this.props.children, child =>
					React.cloneElement(child, {
						activeTab: this.state.activeTab,
						addTab: this.addTab,
					}),
				)}
			</div>
		)
	}
}
